import React, { ReactNode } from 'react'
import styled, { StyledComponent } from 'styled-components'
import { WidthProperty, HeightProperty, TopProperty, LeftProperty } from 'csstype'

interface IFixedElementProps {
  children: ReactNode
  top?: TopProperty<1>
  left?: LeftProperty<1>
  width?: WidthProperty<1>
  height?: HeightProperty<1>
}

const FixedElement: StyledComponent<'div', any, IFixedElementProps, never> = styled.div`
  position: fixed;
  width: ${(props: IFixedElementProps) => props.width || '100%'};
  height: ${(props: IFixedElementProps) => props.height || '100%'};
  top: ${(props:IFixedElementProps) =>  props.top || '0'};
  left: ${(props:IFixedElementProps) => props.left || '0'};
`

export const Fixed = ({ children, top, left, width, height }: IFixedElementProps) => (
  <FixedElement top={top} left={left} width={width} height={height}>{children}</FixedElement>
)
